<div class="grd-row-col-3-6"><table class="p1"><tr><th>amount of trades</th><td>{{ report.trades }}</td></tr><tr><th>sharpe ratio</th><td>{{ round2(report.sharpe) }}</td></tr><tr><th>start balance</th><td>{{ round(report.startBalance) }} {{ report.currency }}</td></tr><tr><th>final balance</th><td>{{ round(report.balance) }} {{ report.currency }}</td></tr><tr><th>simulated profit</th></tr></table><div class="big txt--right price" :class="profitClass">{{ round(report.relativeProfit) }}%</div></div>