<div class="grd contain"><div class="grd-row"><div class="grd-row-col-3-6 mx1"><h3>Market</h3><market-picker v-on:market="updateMarketConfig" :only-tradable="isTradebot"></market-picker></div><div class="grd-row-col-3-6 mx1"><type-picker v-on:type="updateType"></type-picker></div></div><template v-if="type !== &quot;market watcher&quot;"><div class="hr"></div><strat-picker class="contain my2" v-on:stratConfig="updateStrat"></strat-picker><div class="hr" v-if="type === &quot;paper trader&quot;"></div><paper-trader v-on:settings="updatePaperTrader" v-if="type === &quot;paper trader&quot;"></paper-trader></template></div>