<div><h3>Select a dataset</h3><div class="txt--center my2" v-if="datasetScanstate === &quot;idle&quot;"><a class="w100--s btn--primary scan-btn" href="#" v-on:click.prevent="scan">Scan available data</a></div><div class="txt--center my2" v-if="datasetScanstate === &quot;scanning&quot;"><spinner></spinner></div><div class="my2" v-if="datasetScanstate === &quot;scanned&quot;"><div v-if="datasets.length != 0"><table class="full"><thead><tr><th> </th><th>exchange</th><th>currency</th><th>asset</th><th>from</th><th>to</th><th>duration</th></tr></thead><tbody><tr v-for="(set, i) in datasets"><td class="radio"><input type="radio" name="dataset" :value="i" v-model="setIndex" v-bind:id="set.id"></td><td> <label v-bind:for="set.id">{{ set.exchange }}</label></td><td> <label v-bind:for="set.id">{{ set.currency }}</label></td><td><label v-bind:for="set.id">{{ set.asset }}</label></td><td> <label v-bind:for="set.id">{{ fmt(set.from) }}</label></td><td> <label v-bind:for="set.id">{{ fmt(set.to) }}</label></td><td><label v-bind:for="set.id">{{ humanizeDuration(set.to.diff(set.from)) }}</label></td></tr></tbody></table><a class="btn--primary" href="#" v-on:click.prevent="openRange" v-if="!rangeVisible">Adjust range</a><template v-if="rangeVisible"><div><label for="customFrom">From:</label><input v-model="customFrom"></div><div><label for="customTo">To:</label><input v-model="customTo"></div></template></div><em v-else>No Data found <a href="#/data/importer">Lets add some</a></em></div></div>