<div class="contain my2"><div v-if="data &amp;&amp; !data.done"><h2>Importing data..</h2><div class="grd"><div class="grd-row"><div class="grd-row-col-2-6"><strong>Market:</strong></div><div class="grd-row-col-4-6">{{ data.watch.exchange }}</div></div><div class="grd-row"><div class="grd-row-col-2-6"><strong>Currency/Asset:</strong></div><div class="grd-row-col-4-6">{{ data.watch.currency }}/{{ data.watch.asset }}</div></div></div><div class="grd"><div class="grd-row"><div class="grd-row-col-2-6"><strong>From:</strong></div><div class="grd-row-col-4-6">{{ fmt(from) }}</div></div><div class="grd-row"><div class="grd-row-col-2-6"><strong>To:</strong></div><div class="grd-row-col-4-6">{{ fmt(to) }}</div></div><div class="grd-row" v-if="initialized"><div class="grd-row-col-2-6"><strong>Imported data until:</strong></div><div class="grd-row-col-4-6">{{ fmt(latest) }}</div></div><div class="grd-row" v-if="initialized"><div class="grd-row-col-2-6"><strong>Remaining:</strong></div><div class="grd-row-col-4-6">{{ fromEnd }}</div></div></div><spinner v-if="!initialized"></spinner><div class="contain" v-if="initialized"><progressBar :progress="progress"></progressBar></div><p> <em>(you don't have to wait until the import is done,you can already start <router-link to="/backtest">backtesting</router-link>).</em></p></div><div class="txt--center" v-if="data &amp;&amp; data.done"><h2>Import done</h2><p> Go and <router-link to="/backtest">backtest</router-link> with your new data!</p></div><div class="txt--center" v-if="!data"><h2>ERROR: Unknown import</h2><p> <I>don't know this import..</I></p></div></div>