<div class="contain"><div class="grd-row summary"><div class="grd-row-col-3-6"><table class="p1"><tr><th>start time</th><td>{{ report.startTime }}</td></tr><tr><th>end time</th><td>{{ report.endTime }}</td></tr><tr><th>timespan</th><td>{{ report.timespan }}</td></tr><tr><th>start price</th><td>{{ round(report.startPrice) }} {{ report.currency }}</td></tr><tr><th>end price</th><td>{{ round(report.endPrice) }} {{ report.currency }}</td></tr><tr><th>market</th><td>{{ round(report.market) }}%</td></tr></table></div><paperTradeSummary :report="report"></paperTradeSummary></div></div>