<div class="contain roundtrips"><h2>Roundtrips</h2><table v-if="roundtrips.length"><thead><tr><th>Entry at (UTC)</th><th>Exit at (UTC)</th><th>Exposure</th><th>Entry balance</th><th>Exit balance</th><th>P&amp;L</th><th>Profit</th></tr><tr v-for="rt in roundtrips"><td>{{ fmt(rt.entryAt) }}</td><td>{{ fmt(rt.exitAt) }}</td><td>{{ diff(rt.duration) }}</td><td>{{ round(rt.entryBalance) }}</td><td>{{ round(rt.exitBalance) }}</td><template v-if="Math.sign(rt.pnl)===-1"><td class="loss">{{ Math.sign(rt.pnl)*rt.pnl.toFixed(2) }}</td><td class="loss">{{ rt.profit.toFixed(2) }}%</td></template><template v-else><td class="profit">{{ rt.pnl.toFixed(2) }}</td><td class="profit">{{ rt.profit.toFixed(2) }}%</td></template></tr></thead></table><div v-if="!roundtrips.length"><p>Not enough data to display</p></div></div>